<template>
  <div class="data-extra-apply-window">
    <div class="topTitle">
      <span class="topTitle-span1">检查数据提取</span>
      <span class="topTitle-span1">
        <img src="../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">提取数据概览</span>
      <div class="button-content">
        <Button type="primary" @click="applyBtn" v-authcontrol="{menuCode:'107001001000'}" v-if="firstLoginFlag">
          <i></i>立即申请
        </Button>
        <!-- <Button type="primary" v-authcontrol="{menuCode:'10700101000', eventBody: applyBtn}">
          <i></i>立即申请
        </Button> -->
        <!-- <Button type="primary">
          <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button>-->
      </div>
    </div>
    <div class="header">
      <div class="content1">
        <!-- <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>
        </div>-->
        <div class="checked-content">
          <span class="title">申请时间</span>
          <DatePicker
            split-panels
            type="daterange"
            placeholder="请选择时间"
            :value="formValidate.applyTime"
            @on-change="formValidate.applyTime=$event"
          ></DatePicker>
        </div>
        <div class="checked-content">
          <span class="title">提取机构</span>
          <span style="width:300px;height:auto;min-height:40px;position:relative;display: inline-block;">
            <treeselect
              v-model="formValidate.extraUnit"
              valueConsistsOf="ALL" 
              :limit="3"
              :multiple="true"
              :options="data1"
              placeholder
              style="position:absolute;top:15px;left:0px"
            />
          </span>
        </div>
        <div class="checked-content">
          <span class="title">审核状态</span>
          <CheckboxGroup v-model="formValidate.workFlowState">
            <Checkbox
              v-for="item in workFlowStates"
              :label="item.code"
              :key="item.code"
            >{{item.name}}</Checkbox>
          </CheckboxGroup>
        </div>
      </div>
    </div>
    <div class="contentCellBox cardBoxParent">
      <div class="contentCellTitle">
        <div class="contentCellTitle-1"></div>信息列表
      </div>
      <div class="contentCellBody">
        <Row v-for="(item,index) in cardArr2" :key="index">
            <div class="cardBox" v-for="(item2,key) in item" :key="key">
              <div class="cardDiv" v-for="item3 in item2" :key="item3.batchId">
                <div class="cardDiv-title">
                  <div class="cardDiv-title-left" @click="goDataExtractionApply(item3, '107001007000')" v-authcontrol="{menuCode:'107001007000'}" v-if="firstLoginFlag">
                    <span class="textOverflow" style="width:200px" :title="key">{{key}}</span>
                  </div>
                  <div class="cardDiv-title-right">
                    <!-- <span class="carDivSpan2" v-if="item3.taskStatus=='2'" @click="verifyItem(item3)">审核</span> -->
                  </div>
                </div>
                <div class="cardDiv-body">
                  <div class="cardDiv-body-cell">
                    <div class="cardDiv-body-cellLeft">提取数据期间:</div>
                    <div class="cardDiv-body-cellRight">{{item3.extraTime}}~{{item3.extraTime2}}</div>
                  </div>
                  <div class="cardDiv-body-cell">
                    <div class="cardDiv-body-cellLeft">申请时间:</div>
                    <div class="cardDiv-body-cellRight">{{item3.applyTime.substr(0,19)}}</div>
                  </div>
                  <div class="cardDiv-body-cell">
                    <div class="cardDiv-body-cellLeft">提交截止日期:</div>
                    <div class="cardDiv-body-cellRight">{{item3.deadLine.substr(0,10)}}</div>
                  </div>
                  <div class="cardStep">
                    <el-steps :active="Number(item3.workflowState)-1" :align-center="true">
                      <template v-for="(it,index3) in item3.bpmDefinitions">
                        <el-step
                          :title="it.nodeName"
                          :key="it.id"
                          status="success"
                          v-if="it.nodeId<item3.workflowState"
                          @click.native="viewVerifyDetail(item3,it.nodeId)"
                        ></el-step>
                        <el-step
                          :title="it.nodeName"
                          :key="it.id"
                          :status="getDisplayStatus(item3.taskStatus)"
                          v-if="it.nodeId==item3.workflowState"
                          @click.native="viewVerifyDetail(item3,it.nodeId)"
                        ></el-step>
                        <el-step
                          :title="it.nodeName"
                          :key="it.id"
                          status="wait"
                          v-if="it.nodeId>item3.workflowState"
                        ></el-step>
                      </template>
                    </el-steps>
                  </div>
                  <div class="cardDiv-body-cell" style="justify-content: center;">
                    <div class="cardDiv-body-cellLeft">处理人:</div>
                    <div class="cardDiv-body-cellRight">{{item3.names[item3.names.length -1]}}</div>
                  </div>
                </div>
                <div class="cardDiv-foot">
                  <div class="card-sts">
                    <template v-for="it in emergeLevels">
                      <p v-if="it.code==item3.emergeLevel">{{it.name}}</p>
                    </template>
                  </div>
                </div>
              </div>
            </div>
        </Row>
      </div>
    </div>
    <Modal :mask-closable="false"  
          v-model="showModal"
          :title="modelTitle"
          @on-cancel="cancel"
          class="dialog"
          :styles="{width: '80%'}"
          >
            <component :dataObj="dataObj" :is="tabview" width="80%" :key="compKey" v-if="showModal"></component>
          <div slot="footer"></div>
      </Modal>
