  methods: {
    cancel(){
    },
    //审核
    verifyItem(item) {
      let menuBox = this.$store.state.menuBox;
      menuBox.forEach((element, index) => {
        element.flag = false;
        if (element.name == "审核任务") {
          element.flag = true;
          // -----------
          this.storage.set("menuBoxIndex", index);
          this.$store.commit("menuBoxIndex", {
            menuBoxIndex: index
          });
          // -----------
        }
      });
      // -----------
      this.storage.set("menuBox", menuBox);
      this.$store.commit("menuBox", {
        menuBox: menuBox
      });
      this.storage.set("leftMenuBox", "");
      this.$store.commit("leftMenuBox", {
        leftMenuBox: ""
      });
      // -----------
      this.$router.push({
        name: "dataAuditExtrackApplyDetail/4",
        query: { documentId: item.documentId }
      });
    },
    selectTreeCleanUp(treeDatas) {
        for (let i = 0; i < treeDatas.length; i++) {
          if (treeDatas[i].children && treeDatas[i].children.length >0) {
              this.selectTreeCleanUp(treeDatas[i].children);
          }
          else this.$delete(treeDatas[i], "children");
        }
    },
    //查看审核详情
    viewVerifyDetail(item, step) {
      this.compKey = item.documentId;
      this.dataObj = {
        documentId: item.documentId,
        name:'数据提取'
      }
      this.showModal = true;
    },
    getDisplayStatus(status) {
      if (status == "0") return "success";
      else if (status == "2") return "process";
      else if (status == "1") return "error";
    },
    applyBtn() {
      this.$router.push({ name: "dataExtractionApply" });
    },
    goDataExtractionApply(item, menuCode){
      this.$router.push({
        name: "dataExtractionApply2",
        query: {
          batchId: item.batchId,
          menuCode: menuCode,
          bpmName: item.bpmDefinitions[0].bpmName,
          disabledFlag: item.taskStatus == "1"?true:false,
          Id: "1"
        }
      });
    }
  }
