<style lang="scss" @scoped>
.cardDiv-title-left {
  overflow: hidden;
}
.cardStep .el-step__main .el-step__title {
  font-size: 10px !important;
}
.dialog .ivu-modal-body {
  margin: 20px 40px 0;
  max-height: 500px;
  overflow-y: auto;
}
.data-extra-apply-window {
  background: #eef3f7;
  overflow: hidden;
  .header {
    height: auto;
    background: #fff;
    padding-left: 15px;
    padding-top: 20px;
    .content1 {
      .checked-content {
        padding: 6px 10px;
        display: inline-block;
        vertical-align: top;
        .title {
          display: inline-block;
          font-size: 16px;
          width: 150px;
          padding-left: 30px;
          text-align: left;
          font-weight: bold;
          vertical-align: top;
        }
        .ivu-radio-group {
          label {
            font-size: 14px;
            margin-right: 15px;
            .ivu-radio-checked .ivu-radio-inner {
              background: #fa9324;
              border-color: #fa9324;
              &::after {
                background: #fa9324;
              }
            }
          }
        }
        .ivu-checkbox-group {
          display: inline-block;
          label {
            font-size: 14px;
            .ivu-checkbox-checked .ivu-checkbox-inner {
              background: #fa9324;
              border-color: #fa9324;
            }
          }
        }
      }
      .content1-1 {
        display: inline-block;
        .ivu-select {
          padding: 12px 0;
          width: 300px;
        }
      }
      &.border-bottom {
        border-bottom: 1px dashed #eeeeee;
      }
      .contentCellTitle {
        display: inline-block;
        width: 100px;
        font-size: 16px;
        vertical-align: top;
        .contentCellTitle-1 {
          width: 3px;
          height: 18px;
        }
      }
      .header-wrapper {
        width: 80%;
        display: inline-block;
        padding: 5px 0;
        .ivu-tag {
          padding: 0px 5px;
          margin: 11px 4px 4px 0px;
          height: 28px;
          line-height: 28px;
          background: #fa9324;
          span {
            &.ivu-tag-text {
              color: #fff;
            }
          }
          &:not(.ivu-tag-border):not(.ivu-tag-dot):not(.ivu-tag-checked) {
            background: 0 0;
            span {
              &.ivu-tag-text {
                color: #b6b6b6;
              }
            }
          }
        }
        span {
          font-size: 14px;
          margin: 0 10px;
          cursor: pointer;
          &.active {
            background: #fa9324;
            border-radius: 2px;
            color: #fff;
          }
        }
      }
    }
  }
}
.data-extra-apply-window .vue-treeselect{
  top: 0px!important;
  position: relative!important;
}
</style>


